import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import Lightbox from 'react-awesome-lightbox';
import 'react-awesome-lightbox/build/style.css';
import './try.css';
import Card from './Card';
import * as XLSX from 'xlsx';

function Try({ OnAddToCart, title, price, id, added = false, ...props }) {
  const [isAddedToCart, setIsAddedToCart] = useState(added);
  const [excelData, setExcelData] = useState(null);
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  const imageUrl = props.imageUrl && props.imageUrl.length > 0 ? props.imageUrl : ["/img/default-image.svg"];

  useEffect(() => {
    const fetchExcelData = async () => {
      try {
        const response = await fetch('https://docs.google.com/spreadsheets/d/11IrWYOEe7F6E0vgKE4fa57H3SnhQAeRPGs73jg2RGvw/export?format=xlsx');
        const buffer = await response.arrayBuffer();
        const workbook = XLSX.read(buffer, { type: 'buffer' });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);
        setExcelData(jsonData);
      } catch (error) {
        console.error('Error fetching Excel data:', error);
      }
    };

    fetchExcelData();
  }, []);

  const shuffleArray = (array) => {
    for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
  };

  const showCards = () => {
    if (!excelData) {
      return (
        <div>Loading...</div>
      );
    }

    const shuffledData = shuffleArray([...excelData]);
    const randomCards = shuffledData.slice(0, 4);
    
    return randomCards.map((item, index) => (
      <Card
        key={index}
        title={item['']}
        price={item['']}
        imageUrl={item[' '].split(',')}
        OnPlus={OnAddToCart}
        OnFavorite={() => console.log("  ")}
        id={item.id}
        
        loading={false}
      />
    ));
  };

  const OnClickPlus = () => {
    if (!isAddedToCart) {
      OnAddToCart({ id, title, imageUrl, price });
      setIsAddedToCart(true);
    }
  };

  return (
    <div className="try">
      <ul className="headerRight1">
        <li>
          <Link to="/">
            <img 
              height={25} 
              width={25} 
              style={{ cursor: 'pointer' }} 
              src="/img/arrowleft.png" 
              alt="Arrow Left" 
            />
          </Link>
          <div className="carousel-container" style={{ marginTop: '60px', marginRight: '70px' }}>
            <Carousel
              showThumbs={false}
              showIndicators={false}
              showStatus={false}
              infiniteLoop={true}
              useKeyboardArrows={true}
              autoPlay={false}
            >
              {imageUrl.map((url, index) => (
                <div 
                  key={index} 
                  className="carousel-image" 
                  onClick={() => { setLightboxOpen(true); setCurrentImageIndex(index); }}
                >
                  <img  src={url} alt={`Motors ${index + 1}`} />
                </div>
              ))}
            </Carousel>
          </div>
        </li>
        <li className="details">
          <h1>{title}</h1>
          <h4>:  Champion</h4>
          <div className="try2">
            <ul className="table">
              <li className="price-section table-row">
                <h1 className="table-cell" style={{ color: 'red', marginRight: '10px' }}>{price}</h1>
              </li>
              <li className="availability-row">
                <p className="availability">  !</p>
              </li>
              <p className="table-row"> : {props.description}</p>
              <li className="lishka table-row">
                <button 
                  onClick={OnClickPlus} 
                  disabled={isAddedToCart}
                  className={isAddedToCart ? "added-to-cart" : ""}
                >
                  {isAddedToCart ? " " : ""}
                </button>
              </li>
              <li className="oplata">
                <h5> , ,        Visa/Mastercard/MonoPay.</h5>
                <img src='/img/visa.svg' alt="Visa" />
                <img src='/img/monopay.svg' alt="MonoPay" />
                <img src='/img/mastercard.svg' alt="MasterCard" />
              </li>
            </ul>
          </div>
        </li>
        <li className="advantages">
          <h3> :</h3>
          <ul className="advantages-list">
            <li className="advantage-item">
              <img 
                className="advantage-image"
                src='/img/dostavka.png' 
                alt=" " 
              /> 
              <span> </span>
            </li>
            <li className="advantage-item">
              <img 
                className="advantage-image"
                src='/img/garan.png' 
                alt="   " 
              />
              <span>   </span>
            </li>
          </ul>
          <ul className="advantages-list" style={{marginTop:'40px'}}>
            <li className="advantage-item">
              <img 
                className="advantage-image"
                src='/img/garan.png' 
                alt="   " 
              />
              <span>   </span>
            </li>
            <li className="advantage-item">
              <img 
                className="advantage-image"
                src='/img/garan.png' 
                alt=" " 
              />
              <span> </span>
            </li>
          </ul>
          <div className='delivery'>
            <h3> :</h3>
            <ul>
              <li className="delivery-item">
                <img src='/img/post-office.svg' className="delivery-icon" alt="" />
                <div>
                  <span>:</span>
                  <h5>   <br />
                      . , 110, <br />
                      ,  , 79000
                  </h5>
                  </div>
              </li>
              <li className="delivery-variantu">
                <img src='/img/nova-poshta.svg' className="delivery-icon" alt=" " />
                <div>
                  <span>   </span>
                  <h5>   2500 </h5>
                </div>
              </li>
              <li className="delivery-variantu">
                <img src='/img/nova-poshta.svg' className="delivery-icon" alt=" " />
                <div>
                 
                <span>   </span>
                  <h5>   2500 </h5>
                </div>
              </li>
              <li className="delivery-variantu">
                <img src='/img/ukr-poshta.svg' className="delivery-icon" alt="" />
                <div>
                  <span>  </span>
                  <h5>   2500 </h5>
                </div>
              </li>
            </ul>
          </div>
        </li>
      </ul>
      
      
      <div style={{marginTop:'100px'}} className='recommendations'>
        
        <div style={{display:'flex',alignItems:'center'}} className='cards-container'>
        <h2 style={{marginRight:'20px',marginBottom:'20px'}}> :  </h2>
          {showCards()}
        </div>
      </div>
      
      {lightboxOpen && (
        <Lightbox 
          images={imageUrl.map((url) => ({ url }))}
          onClose={() => setLightboxOpen(false)}
          startIndex={currentImageIndex}
        />
      )}
    </div>
  );
}

export default Try;
